import React, { Component } from 'react';
import { Translate } from 'react-i18nify';
import './RentPage.css'
import './Ribbon.css'

class Store extends Component {
    constructor(props) {
        super(props);
        this.state={
            zoomed: false,
        }
    }
    render() {
        return <div class="store">
                    <div class="store-icon">
                        <img alt="store" class="store-image" src={this.props.store.name + '_small.jpg'} onClick={()=>{this.setState({zoomed: true})}}/>
                    </div>
                    <div class="store-data">
                        <div class = "store-header">
                            <Translate value={'storeNames.' + this.props.store.name} tag="h2"/>
                        </div>
                        <div class = "store-info">
                            <Translate value="stores.area"/>    
                        <span>{this.props.store.area} m<sup>2</sup></span>
                        </div>
                        <div class = {"store-info"+ (!this.props.store.places ? ' hidden' : '')}>
                            <Translate value="stores.pallets"/>    
                            <span>{this.props.store.places}</span>
                        </div>
                        <div class = {"ribbon ribbon-top-right " + (this.props.store.insulated ? 'blue' : 'orange')}>
                            {this.props.store.insulated ? <Translate value="stores.insulated"/> : <Translate value="stores.notInsulated"/>}
                        </div>
                    </div>
                    <div class={"image-container "+ (this.state.zoomed ? 'visible' : 'invisible')}
                        onClick={()=>{this.setState({zoomed: false})}}>
                        <img alt="store" class="store-image-big" src={this.props.store.name + '_big.jpg'}/>
                    </div>
               </div>

    }
}

class RentPage extends Component {
    stores = [
        {id: 1, name: 'room1', area: 942, places: 1668, stairs: 3, insulated: true, free: true},
        {id: 2, name: 'room2', area: 962, places: 1267, stairs: 3, insulated: true, free: true},
        {id: 3, name: 'room3', area: 962, places: 1326, stairs: 3, insulated: true, free: true},
        {id: 4, name: 'room45', area: 2308, places: 3791, stairs: 3, insulated: true, free: true},
        {id: 6, name: 'roomA', area: 1482, places: 2273, stairs: 3, insulated: true, free: false},
        {id: 7, name: 'roomB', area: 1520, places: 2868, stairs: 3, insulated: true, free: false},
        {id: 8, name: 'roomC', area: 1520, places: 2600, stairs: 3, insulated: true, free: false},
        {id: 9, name: 'roomD', area: 1520, places: 2868, stairs: 3, insulated: true, free: false},
        {id: 10, name: 'roomE', area: 684, places: 1330, stairs: 3, insulated: true, free: false},
        {id: 11, name: 'roomF', area: 662, places: 1330, stairs: 3, insulated: true, free: false},
        {id: 12, name: 'roomG', area: 989, places: 2700, stairs: 4, insulated: true, free: true},
        {id: 13, name: 'roomH', area: 989, places: 2700, stairs: 4, insulated: true, free: true},
        {id: 14, name: 'onionStore', area: 942, insulated: false, free: true},
        {id: 15, name: 'wrapperStore', area: 942, insulated: false, free: true},
    ];

    render() {
        let storeList = <div class="store-list">{this.stores.filter((storeItem) => {return storeItem.free}).map(storeItem => {return <Store store={storeItem}/>})}</div>;
        return <div class="page rent-page">
                    <div class="inner-content">
                        <Translate value="stores.title" tag="h1"/>
                        {storeList}
                    </div>
               </div>
    }
}

export default RentPage;

