import React, { Component } from 'react';
import { Link } from "react-router-dom"
import { Translate } from 'react-i18nify';
import './AboutUs.css'
import './Ribbon.css'

class AboutUs extends Component {

    render() {
        let storesLink = <Link to="/stores"><Translate value="menu.stores"/></Link>;
        let contactLink = <Link to="/contacts"><Translate value="menu.contacts"/></Link>;

        return <div class="page rent-page">
                    <div class="inner-content">
                        <Translate value="aboutUs.title" tag="h1"/>
                        <div class="content-wrapper">
                        <img alt="Coldstore" src="coldstore.jpg" class="airphoto"/>
                        <Translate value="aboutUs.paragraphs.first" dangerousHTML tag="p"/>
                        <Translate value="aboutUs.paragraphs.second" tag="p"/>
                        <p>
                        <Translate value="aboutUs.paragraphs.third1"/> {storesLink} 
                        <Translate value="aboutUs.paragraphs.third2"/> {contactLink}
                        <Translate value="aboutUs.paragraphs.third3"/>
                        </p>
                        </div>
                    </div>

               </div>
    }
}

export default AboutUs;


