import React, { Component } from 'react';
import { Link } from "react-router-dom"
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faBars } from '@fortawesome/free-solid-svg-icons'
import { I18n, Translate } from 'react-i18nify';
import './Translations.js'
import './Header.css';


class Header extends Component {

    constructor(props) {
        super(props);
        I18n.setLocale('hu');
        this.state = {
            showDropdownMenu: false,
        }
    }

    render() {
        return <div class="main-header">
            <img alt="Alföldi Hűtőház" class="title" src="title.png"/>
            <div class="hamburger-menu" onClick={()=>{this.setState({showDropdownMenu: !this.state.showDropdownMenu})}}>
                <FontAwesomeIcon icon={faBars}/>
            </div>
            <ul class={"main-menu " + (this.state.showDropdownMenu ? 'visible' : 'invisible')} 
                onClick={()=>{this.setState({showDropdownMenu: false})}}>
                <li>
                    <Link to="/"><Translate value='menu.aboutUs'/></Link>
                </li>
                <li>
                    <Link to="/stores"><Translate value='menu.stores'/></Link>
                </li>
                <li>
                    <Link to="/contacts"><Translate value='menu.contacts'/></Link>
                </li>
                <li class="hamburger-language-selector">        
                    <div class="responsive-language-selector">
                        <img alt="hu" src="hu.png" class="flag flag-hu" onClick={()=>{I18n.setLocale('hu');}}/>
                        <img alt="en" src="uk.png" class="flag flag-en" onClick={()=>{I18n.setLocale('en');}}/>
                        <img alt="de" src="de.png" class="flag flag-de" onClick={()=>{I18n.setLocale('de');}}/>
                    </div>
                </li>
            </ul>
            <div class="language-selector">
                <img alt="hu" src="hu.png" class="flag flag-hu" onClick={()=>{I18n.setLocale('hu');}}/>
                <img alt="en" src="uk.png" class="flag flag-en" onClick={()=>{I18n.setLocale('en');}}/>
                <img alt="de" src="de.png" class="flag flag-de" onClick={()=>{I18n.setLocale('de');}}/>
            </div>
            </div>
    }
}

export default Header;
