import React, { Component } from 'react';
import './ContactPage.css';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faMobileAlt } from '@fortawesome/free-solid-svg-icons'
import { faMapMarkerAlt } from '@fortawesome/free-solid-svg-icons'
import { faMapMarkedAlt } from '@fortawesome/free-solid-svg-icons'
import { faEnvelope } from '@fortawesome/free-regular-svg-icons'
import { faUserCircle } from '@fortawesome/free-regular-svg-icons'
import { Translate } from 'react-i18nify';
import Obfuscate from 'react-obfuscate'

class Namecard extends Component {
    render() {
            let avatar = (!this.props.img) ? <FontAwesomeIcon icon={faUserCircle}/> : <img alt="avatar" src={this.props.img}/>;
            return  <div class="namecard">
                        <div class="avatar">{avatar}</div>
                        <div class="person-contacts">
                            <Translate value="contacts.personName" firstName={this.props.firstName} lastName={this.props.lastName} tag="h2"/>
                            <span class="icon"><FontAwesomeIcon icon={faMobileAlt}/></span><Obfuscate tel={this.props.mobile}/><br/>
                            <span class="icon"><FontAwesomeIcon icon={faEnvelope}/></span><Obfuscate email={this.props.email}/>
                        </div>
                    </div>
    }
}


class ContactPage extends Component {

    render() {
        return <div class="page contact-page">
                    <div class="inner-content">
                        <div class="address">
                            <div><Translate value="contacts.companyName" tag="h2"/>
                            <span class="icon"><FontAwesomeIcon icon={faMapMarkedAlt}/></span><Translate value="contacts.address"/><br/>
                            <span class="icon"><FontAwesomeIcon icon={faMapMarkerAlt}/></span>46°39'44.5"N 21°05'22.3"E</div>
                        </div>
                        <div class="map">
                            <iframe title="map" src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d10953.346847889721!2d21.081289917374985!3d46.65961302998041!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x0%3A0x0!2zNDbCsDM5JzM0LjYiTiAyMcKwMDUnMjQuMiJF!5e0!3m2!1sen!2shu!4v1545257789168" width="400" height="200" frameborder="0"></iframe>
                        </div>
                        <Namecard lastName="Szabó" firstName="Miklós" mobile="+36 30 219 3766" email="szabo.miklos@alfoldihutohaz.hu"/>
                        <Namecard lastName="Szirbucz" firstName="Tamás" mobile="+36 70 411 3157" email="szirbucz.tamas@alfoldihutohaz.hu" img="tomi.png"/>
                    </div>
               </div>
    }
}

export default ContactPage;


